@import url(https://fonts.googleapis.com/css?family=Sacramento&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Indie+Flower&family=Yellowtail&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(to left,rgb(140, 9, 248)0%,rgb(243, 227, 2)100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.contanier{
  position: relative;
  /* top:110px; */
  margin-top: 7vh;
  display: flex;
  justify-content: center;
}.winnerClass{
  font-size: calc(20px + 5vh);
  line-height: calc(20px + 5vh);
  color: blueviolet;
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
  color: #fff6a9;
  font-family: 'Indie Flower', cursive;
  text-align: center;
  animation: blink 5s infinite;
  -webkit-animation: blink 5s infinite;
}
.button{
  display: flex;
  justify-content: center;
  align-items: center;
  
  font-family: 'Indie Flower', cursive;
}
@-webkit-keyframes blink {
  20%,
  24%,
  55% {
    color: rgb(59, 1, 65);
    text-shadow: none;
  }
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
/*     color: #fccaff;
    text-shadow: 0 0 5px #f562ff, 0 0 15px #f562ff, 0 0 25px #f562ff,
      0 0 20px #f562ff, 0 0 30px #890092, 0 0 80px #890092, 0 0 80px #890092; */
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}
@keyframes blink {
  20%,
  24%,
  55% {
    color:rgb(59, 1, 65);
    text-shadow: none;
  }
  0%,
  19%,
  21%,
  23%,
  25%,
  54%,
  56%,
  100% {
/*     color: #fccaff;
    text-shadow: 0 0 5px #f562ff, 0 0 15px #f562ff, 0 0 25px #f562ff,
      0 0 20px #f562ff, 0 0 30px #890092, 0 0 80px #890092, 0 0 80px #890092; */
  text-shadow: 0 0 5px #ffa500, 0 0 15px #ffa500, 0 0 20px #ffa500, 0 0 40px #ffa500, 0 0 60px #ff0000, 0 0 10px #ff8d00, 0 0 98px #ff0000;
    color: #fff6a9;
  }
}
.square{
    height: 100px;
    width: 100px;
    background-color: white;
    color:black;
    font-size:2.5em;
    
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    border-radius:15%;
}
.square:hover{
    box-shadow:5px 5px 5px 2px rgb(195, 132, 255);
}
.squaresContainer{
    display: inline-grid;
    grid-template-columns: auto auto auto;
    grid-gap:15px;
    gap:15px;
    background-color: cornflowerblue;
    border: cornflowerblue solid 11px; 
    border-radius:10%;
}
